.transactions {
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #db9600;

  .container {
    width: 80%;
    height: 80%;
    border-radius: 5px;
    padding: 10px;
    background-color: #101010;
    color: #fff;
    text-align: center;

    display: flex;
    // align-items: center;
    // justify-content: center;
    flex-direction: column;
    gap: 10px;
    position: relative;

    .back {
      position: absolute;
      top: 10px;
      right: 10px;
      // border: 1px solid red;

      button {
        width: 100%;
        height: 100%;
        background-color: transparent;
        border: none;
        color: #fff;
        font-size: 20px;
        opacity: 0.5;

        &:hover {
          // color: #f5b120;
          opacity: 1;
          cursor: pointer;
        }
      }
    }

    .title {
      h1 {
        font-size: 50px;
        color: #f5b120;
      }
      p {
        font-size: 15px;
      }
    }

    .content {
      width: 100%;
      height: 500px;
      overflow-y: scroll;
    }

    .table {
      // border: 1px solid red;
      width: 100%;

      th {
        background-color: #db9600;
        padding: 10px;
      }

      td {
        padding: 5px;
        background-color: #fff;
        color: #101010;
      }
    }
  }
}

@media screen and (max-width: 600px) {
  .transactions {
    // height: auto;
    .container {
      width: 100%;
      height: 100%;
      border-radius: 0;

      .title {
        h1 {
          font-size: 35px;
        }
        p {
          font-size: 11px;
        }
      }

      .content{
        border: 1px solid red;
      }

      .table{
        font-size: 10px;
        th{
          padding: 5px;
        }
        td{
          padding: 3px;
        }
      }
    }
  }
}
